<template>
  <div>
      <div class="first-section">
        <div class="function-type">
            <button @click ="fTypeSigmoid = !fTypeSigmoid"> Keisti funkcijos tipą
            </button>
            {{fTypeSigmoid ? 'Sigmoidinė' : 'Slenkstinė'}}
            <img :src="getFunctionImagePath">
        </div>
        <label for="input-count">Įvesčių skaičius (X):</label>
        <input type="number" id="input-count" v-model='xNumber'>
        <button @click="xNumberLoopIterations = parseInt(xNumber)"> Generuoti</button>
        <div class="perceptron-input--container" v-if ="xNumberLoopIterations!=0">
            <div class="input-container" v-for="(it) in xNumberLoopIterations" :key ="it">
                <div class="iteration">{{it-1}}</div>
                <div class="input-block">
                    <div class="input-wrapper">
                        <label :for="'x'+(it-1)"> x{{it-1}}</label>
                        <input type="number" :id="'x'+(it-1)" v-model='X[it-1]'>
                    </div>
                    <div class="input-wrapper">
                        <label :for="'w'+(it-1)"> w{{it-1}}</label>
                        <input type="number" :id="'w'+(it-1)" v-model='w[it-1]'> 
                    </div>
                </div>
            </div>
            <button @click="calculatePerceptron"> Skaičiuoti</button>
        </div>
        <div v-if="fa!=undefined" class="output"> t reikšmė: {{parseFloat(fa.toFixed(3))}} </div>
      </div>
      <div class="data-table">
          <table>
              <tr>
                  <td>x1</td>
                  <td>x2</td>
                  <td>Norima t reikšmė</td>
              </tr>
              <tr v-for="(i, n) in tableData" :key ="n">
                    <td>{{i['x1']}}</td>
                    <td>{{i['x2']}}</td>
                    <td>{{i['t']}}</td>
              </tr>
          </table>
          <div class="find-values--container">
                <button @click="findWeights"> Rasti w0, w1, w2 </button>
                <div class="value-text" v-if="foundValues"> 
                    Rastos reikšmės: <br/> 
                    <strong> w0:</strong> {{weightValues[0]}}; <br/> 
                    <strong> w1:</strong> {{weightValues[1]}}; <br/> 
                    <strong> w2:</strong> {{weightValues[2]}};
                </div>
          </div>
        </div>
  </div>
</template>

<script>
export default {
    name: 'Perceptron',
    data(){
        return{
            xNumber: 0,
            xNumberLoopIterations: 0,
            X: [1],
            a: 0,
            w: [],
            fa: undefined,
            fTypeSigmoid: false,
            tableData: [{x1: -0.2, x2: 0.5, t: 0}, {x1: 0.2, x2: -0.5, t: 0}, {x1: 0.8, x2: -0.8, t: 1}, {x1: 0.8, x2: 0.8, t: 1}],
            weightValues: [],
            foundValues: false,
       }
    },
    methods:{
        calculatePerceptron(){
            var sum = 0;
                for(var i = 0;  i< this.xNumberLoopIterations; i++){
                    sum += parseFloat(this.w[i])*parseFloat(this.X[i]);
                }
            this.a = sum;
            if(this.fTypeSigmoid) this.fa = 1/(1+Math.pow(Math.E, (-sum)));
            else{
                  if(sum>=0) this.fa = 1
                  else this.fa = 0;
            }
        },
        findWeights(){
            var possibleValues = [];
            this.foundValues =false;
            var found = false;
            var sum = 0;
            for(var i = -1; i<=1 && !found; i+=0.1){
                for(var j = -1; j<=1 && !found; j+=0.1){
                    for(var k = -1; k<=1 && !found; k+=0.1){
                        for(var l = 0; l < this.tableData.length; l++){
                            sum = (1*i.toFixed(2)+this.tableData[l]['x1']*j.toFixed(2)+this.tableData[l]['x2']*k.toFixed(2));
                            if(this.fTypeSigmoid){
                                if((1/(1+Math.pow(Math.E, (-sum)))) <0.35){
                                    if(this.tableData[l]['t'] == 0) possibleValues[l] = true;
                                    else possibleValues[l] = false;
                                }
                                else if((1/(1+Math.pow(Math.E, (-sum)))) >=0.75){
                                     if(this.tableData[l]['t'] == 1) possibleValues[l] = true;
                                     else possibleValues[l] = false;
                                }
                            }
                            else{
                                if(sum>=0){
                                    if(this.tableData[l]['t'] == 1) possibleValues[l] = true;
                                    else possibleValues[l] = false;
                                }
                                else{
                                    if(this.tableData[l]['t'] == 0) possibleValues[l] = true;
                                    else possibleValues[l] = false;
                                }
                            }
                        }
                        if(possibleValues.every(x => x === true)) {
                            this.weightValues[0] = parseFloat(i.toFixed(2));
                            this.weightValues[1] = parseFloat(j.toFixed(2));
                            this.weightValues[2] = parseFloat(k.toFixed(2));
                            found = true;
                            this.foundValues =true;
                            break;
                        }


                    }
                }
            }
                
            
        }

    },
    computed:{
        getFunctionImagePath(){
            // var path = '';
            if(this.fTypeSigmoid) return require(`../assets/sigmoid.png`);
            else return require(`../assets/threshold.png`);

        }
    }
}
</script>

<style lang="scss">
    .first-section{
        display: inline-block;
        margin: 0 auto;
        padding: 15px;
        min-width: 20vw;
        width: 450px;
        max-width: 460px;
        border-radius: 15px;
        -webkit-box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.75);
        box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.75);
    }
    .input-container{
        display: flex;
        justify-content: center;
    }
    .iteration{
        align-self: center;
        margin-right: 18px;
        padding-right: 15px;
        line-height: 2.5;
        border-right: 2px solid #e0e0e0;
    }
    button{
        background: radial-gradient(circle, rgba(232,231,231,1) 14%, rgba(218,218,218,1) 83%);
        border: 1px solid #c4c4c4;
        border-radius: 15px;
        padding: 4px 12px;
        color: #333333;
        font-weight: 700;
        text-transform: uppercase;
        cursor: pointer;
    }
    button:focus{
        outline: 0;
    }
    .perceptron-input--container{
        margin-top: 10px;
    }
    input{
        margin: 8px 15px 15px 5px;
        border: 2px solid #d8d8d8;
        border-radius: 5px;
    }
    .input-block{
        display: flex;
        justify-content: center;
    }
    .output{
        margin: 15px;
    }
    .function-type{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin: 15px 0; 
    }
    .function-type button{
        margin-right: 10px;
    }
    .function-type img{
        height: 35px;
        margin-left: 15px;
    }
    .data-table{
        display: flex;
        flex-direction: row;
        margin: 0 auto;
        min-width: 300px;
        max-width: 450px;
        padding: 15px;
        border-radius: 15px;
        margin-top: 15px;
        -webkit-box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.75);
        box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.75);
        table{
             flex: 1;
             border: 1px solid gray;
             border-collapse: collapse;
            td{
                border: 1px solid gray;
                padding: 5px;
            }
        }
        button{
            min-height: 28px;
        }
    }
    .find-values--container{
        display: flex;
        flex: 1;
        flex-direction: column;
        justify-content: center;
        button{
            align-self: center;
            max-width: 75%;
        }
        .value-text{
            max-width: 75%;
            margin: 0 auto;
            margin-top: 10px;
            text-align: left;
        }
    }
    @media only screen and (max-width: 500px)  {
        .first-section{
            width: auto;
        }
        .input-block{
            flex-wrap: wrap;
            justify-content: flex-start;
        }
        label{
            margin-right: 3px;
        }
        .input-wrapper{
            width: 90%;
            input{
                width: 100%;
            }
        }

    }
</style>